/*///headersearch start////*/
header .headersearch input, header .headersearch select{
	height: 40px!important;
	margin-top: 2px!important;
	border-radius: 0px!important;
	min-width: 260px;
	border: none!important;
	box-shadow: 0 -1px 0 #dddddd inset !important;
}
header .headersearch .form-group button{
  border-radius: 0 2px 2px 0 !important;
  box-shadow: none;
  height: 40px!important;
  margin-top: 2px!important;
  line-height: 20px!important;
  font-size: 16px;
  padding-left: 15px!important;
  padding-right: 15px!important;
}
header .headersearch select{
	min-width: 130px!important;
}
header .headersearch .form-group{
	padding: 0px;
	margin: 0px;
}
header .headersearch ul{
	padding: 0px;
	margin: 0px;
	list-style: none;
}
header .headersearch ul li{
	float: left;
}
header .headersearch .form-group button{
	border-radius: 0px 2px 2px 0px!important;
	height: 35px;
	box-shadow: none;
}
header .headersearch .dropdown-menu{
	width: 645px;
}
header .headersearch .dropdown-menu li{
	display: block;
	float: left;
}
header .headersearch{
	position: absolute;
	top: 50%;
	margin-top: -22px;
	left: 50%;
	margin-left: -326px;
	background-color: transparent!important;
	border-radius: 0px;
}
/*///headersearch end////*/

.lefthead img{
	width: 100%;
	max-width: 135px;
}
.righthead .notification{position: relative;}
.righthead .notification a{
	position: relative;
	top: 7px;
}
.righthead .notification span{
	position: absolute;
	display: block;
