/*/////EventOrgBox////*/
.eventorgBox .media-list{
	background-color: #fff;
	padding: 5px;
	margin-top: 15px;
	box-shadow: inset 0px 0px 1px #999;
	position: relative;
	min-height: 60px;
}
.eventorg{
	position: relative;
}
.eventorg .iconbox{
	position: absolute;
	bottom: 0px;
	right: 5px;
}
.iconbox a{
	font-size: 16px;
	color: gray;
	margin-left: 5px;
}


/*///addClass//*/
.favclass{color: red !important;}
.eventorgBox h1{font-size: 5vmin;}
.eventorgBox .media-left button{
	background-color:#C95240;
	border: 1px solid #C95240;
	padding: 7px 5px;
	width: 65px;
	height: 50px;
	font-weight: bold;
	font-size: 20px;
	line-height: 22px;
	color: #fff;
}
.map{
	background-color: #ddd!important;
	border: 1px solid #ddd!important; 
}
.map img{
	width: 100%;
	max-width: 40px;
	margin-top: -4px;
}
.media-left button span{
	display: inline-block;
	margin-top: -5px;
}
.eventorgBox .media-body span{
	font-weight: 700;
}

/*////ticketbox///*/
.ticketbox{
	height: auto;
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
	background-color: #fff;
}
.ticketbox .btn-com{
	margin-right: 15px;
	margin-bottom: 15px;
}
.ticketbox .table > tbody > tr > td{
  line-height: 1.42857;
  padding: 5px 12px;
  text-align: left;
  vertical-align: middle;
}
.ticketbox .table thead{
	background-color: #4285F4;
	padding: 8px 12px;
	color: #fff;
}
.ticketbox .table > thead > tr > th{border-bottom:none; padding: 8px 12px;}
.table-responsive{border: none!important}

/*///pricebox table///*/
.pricebox table{
	border: 1px solid #ddd!important;
	float: right;
	margin-right: 12px;
	margin-bottom: 12px;
}
.pricebox table tr td{
	padding: 6px 12px;
}
.pricebox table tr:last-child{
	border-top: 1px solid #ddd;
	font-weight: bold;
	font-size: 14px;
	text-transform: uppercase;
}
.pricebox table tr td:first-child{min-width: 120px;}
.pricebox table tr td:nth-child(2){width: 50px; text-align: center;}

