	padding: 0px;
	margin: 0px;
	color: #999!important;
	line-height: 30px;
}
.footerright ul{
	padding: 0px;
	margin: 0px;
	list-style: none;
}
.footerright ul li{
	float: left;
}
.footerright ul li a{
	width: 30px;
	height: 30px;
	font-size: 22px;
	text-align: center;
	line-height: 30px;
	margin-left: 5px;
	color: #F4F4CF;
