/*==========================Home Page CSS start============*/
.searchfix{position: relative;}
.searchbox{
	position: absolute;
	top: 50%;
	margin-top: -20px;
	left: 50%;
	margin-left: -364px;
	border-radius: 0px 3px 3px 0px;
}
.searchbox select,.searchbox input[type="text"]{
	border-left: 1px solid #108AEB!important;
	min-height: 40px!important;
	border-radius: 0px!important;
	width: 160px;
}
.searchbox input[type="text"]{
	width: 260px;
}
.searchbox .form-group{
	padding: 0px;
	margin: 0px;
}
.searchbox ul{
	padding: 0px;
	margin: 0px;
	list-style: none;
}
.searchbox ul li{float: left;}
.searchbox .searchmain button{
	border: none;
	border-radius: 0px 2px 2px 0px!important;
	height: 40px!important;
