.left-menu ul li a,
a.active1.more-submenu.collapsed,
a.active1.more-menu.collapsed{
 font-size: 14px;
 display: block;
 color: #666666;
 padding: 5px 10px;
 border-bottom: 1px solid #ddd;
 text-decoration: none;
 -webkit-transition:500ms;
 -moz-transition:500ms;
 transition:500ms;
 position: relative;
}
.left-menu ul li a.more-menu.collapsed span:before, 
.left-menu ul li a.more-submenu.collapsed span:before
{
 content: '';
 font-size: 19px;
 line-height: 16px;
 text-align: center;
 position: absolute;
 border-right:3px solid #ccc;
 border-bottom: 3px solid #ccc; 
 transform: rotate(-45deg);
 -webkit-transform: rotate(-45deg);
 -moz-transform: rotate(-45deg);
 width: 10px;
 height: 10px;
 display: block;
 top: 50%;
 margin-top: -5px;
 right: 15px;
}
.left-menu ul li a.more-menu span:before,
.left-menu ul li a.more-submenu span:before
{
 content: '';
 font-size: 19px;
 line-height: 16px;
 text-align: center;
 position: absolute;
 border-right:3px solid #fff;
 border-bottom: 3px solid #fff; 
 transform: rotate(45deg);
 -webkit-transform: rotate(45deg);
 -moz-transform: rotate(45deg);
 width: 10px;
 height: 10px;
 display: block;
 top: 50%;
 margin-top: -5px;
 right: 15px;
}
 a.more-menu.collapsed, a.more-submenu.collapsed{
  background: transparent; color: #666; 
 }
.left-menu ul li a:hover, 
.left-menu ul li a.active,
a.active1.more-menu.collapsed:hover,
.active1.more-menu,
a.active1.more-submenu.collapsed:hover,
.active1.more-submenu{ 
 color: #ffffff;
